import React, { Component } from "react";
import { Link, withRouter, Redirect } from "react-router-dom";
import "./Area.css";
import axios from "axios";
import authService from "../../services/auth-service";
import axiosService from "../../services/axios-service";
import { API_URL } from "../../services/url";
import SimpleReactValidator from "simple-react-validator";
export class AreaAllocate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      emp_name: "",
      region_name: "",
      from_date: "",
      to_date: "",
      area_name : ""
    };
    this.onSubmit = this.onSubmit.bind(this);
    this.validator = new SimpleReactValidator();
  }

  change = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  getWebsite = () => {
    fetch("/").then(console.log(this.state));
  };
  async onSubmit(event) {
    event.preventDefault();
    if (this.validator.allValid()) {
    console.log(this.state);
    try {
      const response = await axios.post(
        `${API_URL}zone/add`,
        this.state,
        (axios.defaults.headers.common["authorization"] =
          "Bearer " + authService.getToken())
      );
      console.log(response);
      //   .then(console.log(this.state));

      //   if (response.data.success) {
      //     alert(response.data.msg);
      //     this.props.history.push("/ZoneLoc");
      //   } else {
      //     alert(response.data.msg);
      //   }
    } catch (error) {
      console.log(error);
    }
  } else {
    this.validator.showMessages();
    // rerender to show messages for the first time
    this.forceUpdate();
  }
  }
  render() {
    return (
      <div className="skin-blue fixed-layout">
        <div className="page-wrapper">
          <div className="container-fluid">
            <div className="row page-titles">
              <div className="col-md-5 align-self-center">
                {/* <h4 className="text-themecolor">Forms</h4> */}
                <nav aria-label="breadcrumb">
                  <ol class="breadcrumb">
                    <li className="breadcrumb-item">
                      <Link to={process.env.PUBLIC_URL + "/"}>Home</Link>
                    </li>
                    <li className="breadcrumb-item active" aria-current="page">
                      Area Allocation
                    </li>
                  </ol>
                </nav>
              </div>
            </div>
          </div>

          {/*Form content begin */}

          <div className="product-form-upper">
            <div className="container">
              <div className="below-custom-form">
                <div className="row">
                  <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <h3>Area Allocation</h3>
                    <br />
                  </div>
                </div>
                <form className="custom-content-form" method="POST">
                  <div className="form-group row">
                    <label for="inputPassword" class="col-sm-1 col-form-label">
                      Employee Name
                    </label>
                    <div class="col-sm-11">
                      <input
                        list="browsers"
                        name="browser"
                        className="form-control"
                        value={this.state.emp_name}
                        name="emp_name"
                        onChange={e => this.change(e)}
                      />
                      <datalist id="browsers">
                        <option value="John" />
                        <option value="Jane" />
                        <option value="Finch" />
                        <option value="Elliot" />
                        <option value="Darlene" />
                      </datalist>
                      <span className="text-danger">
                        {this.validator.message(
                          "name",
                          this.state.emp_name,
                          "required"
                        )}
                      </span>
                    </div>
                  </div>
                  <div class="form-group row">
                    <label
                      for="inputSubcategory"
                      className="col-sm-1 col-form-label"
                    >
                      Region Name
                    </label>
                    <div class="col-sm-11">
                      <select
                        className="form-control"
                        value={this.state.region_name}
                        name="region_name"
                        onChange={e => this.change(e)}
                      >
                        <option value="0">Choose...</option>
                        <option value="1">Region 1</option>
                        <option value="2">Region 2</option>
                        <option value="3">Region 3</option>
                      </select>
                      <span className="text-danger">
                        {this.validator.message(
                          "region",
                          this.state.region_name,
                          "required"
                        )}
                      </span>
                    </div>
                  </div>
                  <div class="form-group row">
                    <label
                      for="inputSubcategory"
                      className="col-sm-1 col-form-label"
                    >
                      Area Name
                    </label>
                    <div class="col-sm-11">
                      <select
                        className="form-control"
                        value={this.state.area_name}
                        name="area_name"
                        onChange={e => this.change(e)}
                      >
                        <option value="0">Choose...</option>
                        <option value="1">Area 1</option>
                        <option value="2">Area 2</option>
                        <option value="3">Area 3</option>
                      </select>
                      <span className="text-danger">
                        {this.validator.message(
                          "area",
                          this.state.area_name,
                          "required"
                        )}
                      </span>
                    </div>
                  </div>
                  <div className="form-row">
                    <div className="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputSubcategory"
                          className="col-sm-2 col-form-label"
                        >
                          From Date
                        </label>
                        <div class="col-sm-10">
                          <input
                            type="date"
                            className="form-control"
                            value={this.state.from_date}
                            name="from_date"
                            onChange={e => this.change(e)}
                          />
                           <span className="text-danger">
                        {this.validator.message(
                          "date",
                          this.state.from_date,
                          "required"
                        )}
                      </span>
                        </div>
                      </div>
                    </div>
                    <div className="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputSubcategory"
                          className="col-sm-2 col-form-label"
                        >
                          To Date
                        </label>
                        <div class="col-sm-10">
                          <input
                            type="date"
                            className="form-control"
                            value={this.state.to_date}
                            name="to_date"
                            onChange={e => this.change(e)}
                          />
                           <span className="text-danger">
                        {this.validator.message(
                          "date",
                          this.state.to_date,
                          "required"
                        )}
                      </span>
                        </div>
                      </div>
                    </div>
                  </div>
                  <button class="btn btn-primary" onClick={this.onSubmit}>
                    Submit
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default AreaAllocate;
