import React, { Component } from "react";
import "./Zone.css";
import { Link } from "react-router-dom";
import axios from "axios";
import authService from "../../services/auth-service";
import axiosService from "../../services/axios-service";

class ViewZone extends Component {
  constructor() {
    super();
    this.state = {
      userdata: [],
      detailsdata: []
    };
    this.handleCheck = this.handleCheck.bind(this);
    this.detailCheck = this.detailCheck.bind(this);
  }
  handleCheck(item) {
    console.log(item.zone_id);
    let sitemeet = item.zone_id;
    fetch(`zone/delete?id=${sitemeet}`, {
      method: "GET",
      headers: {
        //  'Authorization': 'Bearer ' + this.state.token,
        "Content-Type": "application/json"
      }
    })
      .then(function(response) {
        return response.json();
      })
      .then(function(json) {
        if (json.success === true) {
          //   console.log(json);
          alert("Zone has been deleted PLEASE REFRESH THE PAGE");
          //window.location.reload()
          this.handleClick();
        } else {
          console.log(json);
        }
      });
  }
  detailCheck(item) {
    // console.log(item.admin_id);
    let sitemeet = item.zone_id;
    fetch(`zone/detail?id=${sitemeet}`, {
      method: "GET",
      headers: {
        //  'Authorization': 'Bearer ' + this.state.token,
        "Content-Type": "application/json"
      }
    }).then(res => {
      res.json().then(response => {
        console.log(response.zonedata);
        this.setState({ detailsdata: response.zonedata });
        // this.parseJSON(this.state)
      });
    });
  }

  async componentWillMount() {
    await this.handleClick();
  }
  async handleClick() {
    try {
      const response = axios.get(
        "region/view",
        (axios.defaults.headers.common["authorization"] =
          "Bearer " + authService.getToken())
      );
      console.log(response);

      // this.setState({ userdata:response.plantdata})
      // this.parseJSON(this.state)
    } catch (error) {
      console.log(error);
    }
  }
  render() {
    return (
      <React.Fragment>
        <div>
          <div className="skin-blue fixed-layout">
            <div className="page-wrapper">
              <div className="container-fluid">
                <div className="row page-titles">
                  <div className="col-md-5 align-self-center">
                    {/* <h4 className="text-themecolor">Forms</h4> */}
                    <nav aria-label="breadcrumb">
                      <ol class="breadcrumb">
                        <li className="breadcrumb-item">
                          <Link to={process.env.PUBLIC_URL + "/"}>Home</Link>
                        </li>
                        <li
                          className="breadcrumb-item active"
                          aria-current="page"
                        >
                          View Zone
                        </li>
                      </ol>
                    </nav>
                  </div>
                </div>
              </div>
              <div className="custom-table-here">
                <div className="container">
                  <div className="row">
                    <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <table className="table table-hover table-bordered ">
                        <thead className="bg-info custom-thead-color">
                          <tr>
                            <th scope="col">Zone Name</th>
                            <th scope="col">Pin</th>
                            {/* <th scope="col">Action</th> */}
                          </tr>
                        </thead>
                        <tbody>
                          {this.state.userdata ? (
                            this.state.userdata.map(function(item, id) {
                              return (
                                <tr key={id}>
                                  <th scope="row">{item.zone_name}</th>
                                  <td>{item.pin}</td>
                                  {/* <td>
          <Link to ={process.env.PUBLIC_URL +`/EditZone/${item.zone_id}`}><button className="btn btn-warning custom-edit-btn btn-sm"><i class="fa fa-pencil" aria-hidden="true"></i>&nbsp; &nbsp; Edit</button></Link>
          <button  className="btn btn-danger custom-edit-btn btn-sm" onClick={this.handleCheck.bind(this, item)}><i class="fa fa-trash-o" aria-hidden="true"></i>&nbsp;&nbsp;Delete</button>
          <button className="btn btn-success custom-edit-btn btn-sm" onClick={this.detailCheck.bind(this, item)} data-toggle="modal" data-target=".bd-example-modal-lg"><i class="fa fa-eye" aria-hidden="true"></i>&nbsp; &nbsp; Details</button>
          </td> */}
                                </tr>
                              );
                            }, this)
                          ) : (
                            <span>Data is loading....</span>
                          )}
                        </tbody>
                      </table>
                      {/* details modal here */}
                      <div
                        class="modal fade bd-example-modal-lg"
                        tabindex="-1"
                        role="dialog"
                        aria-labelledby="myLargeModalLabel"
                        aria-hidden="true"
                      >
                        <div class="modal-dialog modal-lg">
                          <div class="modal-content">
                            <table className="table table-hover table-bordered ">
                              <thead className="bg-info custom-thead-color">
                                <tr>
                                  <th scope="col">Name</th>
                                  <th scope="col">Pin</th>
                                </tr>
                              </thead>
                              <tbody>
                                {this.state.detailsdata ? (
                                  this.state.detailsdata.map(function(
                                    item,
                                    id
                                  ) {
                                    return (
                                      <tr key={id}>
                                        <th scope="row"> {item.zone_name}</th>
                                        <td>{item.pin}</td>
                                      </tr>
                                    );
                                  },
                                  this)
                                ) : (
                                  <span>Data is loading....</span>
                                )}
                              </tbody>
                            </table>
                            <div class="modal-footer">
                              <button
                                type="button"
                                class="btn btn-secondary"
                                data-dismiss="modal"
                              >
                                Close
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
export default ViewZone;
