import React, { Component } from 'react'
import { Link, Redirect, withRouter, NavLink } from "react-router-dom";
import "./Test.css";
import axios from "axios";
import authService from "../../services/auth-service";
import axiosService from "../../services/axios-service";
import { API_URL } from "../../services/url";
import SimpleReactValidator from "simple-react-validator";
export class BulkDensity extends Component {
    constructor(props) {
        super(props);
        this.state = {
          supplier: "",
          source: "",
          sample_location: "",
          weight: "",
          agg_type: "",
          weight_balance: "",
          weight_sample_testing: "",
          weight_sample_sieving: "",
          material_passing: "",
          pto_comment: "",
          total: "",
          amount_status: false,
          plant: "",
          date: "",
        s_t1 : "",
        s_t2 : "",
        s_t3 : ""
        };
        this.onSubmit = this.onSubmit.bind(this);
        this.validator = new SimpleReactValidator();
      }
      componentWillMount = () => {
        var today = new Date();
        var dd = today.getDate();
        var mm = today.getMonth() + 1; //January is 0!
        var yyyy = today.getFullYear();
    
        if (dd < 10) {
          dd = "0" + dd;
        }
    
        if (mm < 10) {
          mm = "0" + mm;
        }
    
        today = mm + "/" + dd + "/" + yyyy;
        console.log("date here", today);
        this.state.date = today;
        console.log(this.state.date);
      }
      
    
      change = e => {
        this.setState({
          [e.target.name]: e.target.value
        });
      };
      getWebsite = () => {
        fetch("/").then(console.log(this.state));
      };
      async onSubmit(event) {
        event.preventDefault();
        console.log(this.state)
        if (this.validator.allValid()) {
          try {
            const response = await axios.post(
              `${API_URL}employee/add`,
              this.state,
              (axios.defaults.headers.common["authorization"] =
                "Bearer " + authService.getToken())
            );
            console.log(response);
            //   .then(console.log(this.state));
    
            //   if (response.data.success) {
            //     alert(response.data.msg);
            //     this.props.history.push("/ViewEmployee");
            //   } else {
            //     alert(response.data.msg);
            //   }
          } catch (error) {
            console.log(error);
          }
        } else {
          this.validator.showMessages();
          // rerender to show messages for the first time
          this.forceUpdate();
        }
      }
      totalshow = e => {
        e.preventDefault();
        let total_sample_sieving = this.state.weight_sample_sieving;
        let total_sample_testing = this.state.weight_sample_testing;
        let total_amount =
          ((total_sample_testing - total_sample_sieving) / total_sample_testing) *
          100;
        this.state.total = total_amount;
        console.log(total_amount);
        this.setState({
          amount_status: true
        });
      };
      render() {
        let display_total_amount;
        if (this.state.amount_status == true) {
          display_total_amount = <div>{this.state.total} %</div>;
        } else {
          display_total_amount = (
            <div>
              <p>Enter The Above Fields</p>
            </div>
          );
        }
       
        return (
          <div className="skin-blue fixed-layout">
            <div className="page-wrapper">
              <div className="container-fluid">
                <div className="row page-titles">
                  <div className="col-md-5 align-self-center">
                    {/* <h4 className="text-themecolor">Forms</h4> */}
                    <nav aria-label="breadcrumb">
                      <ol class="breadcrumb">
                        <li className="breadcrumb-item">
                          <Link to={process.env.PUBLIC_URL + "/"}>Home</Link>
                        </li>
                        <li className="breadcrumb-item active" aria-current="page">
                         Bulk Density
                        </li>
                      </ol>
                    </nav>
                  </div>
                </div>
              </div>
    
              {/*Form content begin */}
    
              <div className="product-form-upper">
                <div className="container">
                  <div className="below-custom-form">
                    <div className="row">
                      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <div className="custom-btn-lists">
                      <ul>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/ComingSoon"}
                            >
                              Particle Size
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/FLakinessIndex"}
                            >
                              Flakiness Index
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Testtwo"}
                            >
                              Elongation Index
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Testone"}
                            >
                              Slit Content
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Waterabs"}
                            >
                              Gravity and Water abs
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Testthree"}
                            >
                              Surface Moisture
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Compaction"}
                            >
                              Compaction
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/CrushingValue"}
                            >
                              Crushing
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/BulkDensity"}
                            >
                              Bulk Density
                            </NavLink>
                          </button>
                        </li>
                      </ul>
                    </div>
    
                        <hr />
                      </div>
    
                      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <p style={{ textAlign: "center" }}>
                        Test Format - RF:22 (Determination of Dry Loose ad Dry Rodded Bulk Density) 
                        </p>
                        <hr />
                      </div>
                    </div>
                    <form className="custom-content-form">
                      <div className="form-row">
                        <div class="form-group col-md-6">
                          <div class="form-group row">
                            <label
                              for="inputPassword"
                              class="col-sm-2 col-form-label"
                            >
                              Plant
                            </label>
                            <div class="col-sm-10">
                              <input
                                type="text"
                                class="form-control"
                                id="inputPassword"
                                name="plant"
                                value={this.state.plant}
                                onChange={e => this.change(e)}
                              />
                              <span className="text-danger">
                                {this.validator.message(
                                  "plant's name",
                                  this.state.plant,
                                  "required|alpha_space"
                                )}
                              </span>
                            </div>
                          </div>
                        </div>
                        <div class="form-group col-md-6">
                          <div class="form-group row">
                            <label
                              for="inputPassword"
                              class="col-sm-2 col-form-label"
                            >
                              date
                            </label>
                            <div class="col-sm-10">{this.state.date}</div>
                          </div>
                        </div>
                        <div class="form-group col-md-6">
                          <div class="form-group row">
                            <label
                              for="inputPassword"
                              class="col-sm-2 col-form-label"
                            >
                              Supplier
                            </label>
                            <div class="col-sm-10">
                              <input
                                list="browsers"
                                name="supplier"
                                value={this.state.supplier}
                                onChange={e => this.change(e)}
                                className="form-control"
                              />
                              <datalist id="browsers">
                                <option value="supplier 1" />
                                <option value="supplier 2" />
                                <option value="supplier 3" />
                                <option value="supplier 4" />
                                <option value="supplier 5" />
                              </datalist>
                              <span className="text-danger">
                                {this.validator.message(
                                  "supplier's name",
                                  this.state.supplier,
                                  "required"
                                )}
                              </span>
                            </div>
                          </div>
                        </div>
                        <div class="form-group col-md-6">
                          <div class="form-group row">
                            <label
                              for="inputPassword"
                              class="col-sm-2 col-form-label"
                            >
                              Source
                            </label>
                            <div class="col-sm-10">
                              <input
                                type="text"
                                class="form-control"
                                id="inputPassword"
                                name="source"
                                value={this.state.source}
                                onChange={e => this.change(e)}
                              />
                              <span className="text-danger">
                                {this.validator.message(
                                  "source",
                                  this.state.source,
                                  "required"
                                )}
                              </span>
                            </div>
                          </div>
                        </div>
                        <div class="form-group col-md-6">
                          <div class="form-group row">
                            <label
                              for="inputPassword"
                              class="col-sm-2 col-form-label"
                            >
                              Sampling Location
                            </label>
                            <div class="col-sm-10">
                              <input
                                type="text"
                                class="form-control"
                                id="inputPassword"
                                name="sample_location"
                                value={this.state.sample_location}
                                onChange={e => this.change(e)}
                              />
                              <span className="text-danger">
                                {this.validator.message(
                                  "Location of Sample",
                                  this.state.sample_location,
                                  "required|alpha_space"
                                )}
                              </span>
                            </div>
                          </div>
                        </div>
                        <div class="form-group col-md-6">
                          <div class="form-group row">
                            <label
                              for="inputSubcategory"
                              className="col-sm-2 col-form-label"
                            >
                              Weight of empty cup (W)
                            </label>
                            <div class="col-sm-10">
                              <input
                                type="text"
                                class="form-control"
                                id="inputPassword"
                                name="weight"
                                value={this.state.weight}
                                onChange={e => this.change(e)}
                              />
                              <span className="text-danger">
                                {this.validator.message(
                                  "Weight Of Sample",
                                  this.state.weight,
                                  "required|alpha_num_space"
                                )}
                              </span>
                            </div>
                          </div>
                        </div>
                        <div class="form-group col-md-6">
                          <div class="form-group row">
                            <label
                              for="inputPassword"
                              class="col-sm-3 col-form-label"
                            >
                              Aggragate Type
                            </label>
                            <div class="col-sm-9">
                              <div class="form-check form-check-inline">
                                <input
                                  className="form-check-input"
                                  type="checkbox"
                                  id="inlineCheckbox1"
                                  value="option1"
                                />
                                <label
                                  className="form-check-label"
                                  for="inlineCheckbox1"
                                >
                                  20mm
                                </label>
                              </div>
                              <div className="form-check form-check-inline">
                                <input
                                  className="form-check-input"
                                  type="checkbox"
                                  id="inlineCheckbox2"
                                  value="option2"
                                />
                                <label
                                  className="form-check-label"
                                  for="inlineCheckbox2"
                                >
                                  12.5mm
                                </label>
                              </div>
                              <div className="form-check form-check-inline">
                                <input
                                  className="form-check-input"
                                  type="checkbox"
                                  id="inlineCheckbox3"
                                  value="option3"
                                />
                                <label
                                  className="form-check-label"
                                  for="inlineCheckbox3"
                                >
                                  10mm
                                </label>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div class="form-group col-md-6">
                          <div class="form-group row">
                            <label
                              for="inputPassword"
                              class="col-sm-3 col-form-label"
                            >
                              Weighing Balance Details
                            </label>
                            <div class="col-sm-9">
                              <input
                                type="text"
                                class="form-control"
                                id="inputPassword"
                                name="weight_balance"
                                value={this.state.weight_balance}
                                onChange={e => this.change(e)}
                              />
                              <span className="text-danger">
                                {this.validator.message(
                                  "Weight Balance",
                                  this.state.weight_balance,
                                  "required"
                                )}
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>
                      {/* <small>* This Fields are Mandatory . </small> */}
                      {/* <br />
                              <button
                                class="btn btn-primary"
                                onClick={this.onSubmit.bind(this)}
                              >
                                Submit
                              </button> */}
                    </form>
                  </div>
                  {/*etst detial form */}
                  <div className="below-custom-form">
                    <div className="row">
                      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <h3>Test Details</h3>
                        <hr />
                        <br />
                      </div>
                      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                          <p>Calibration of density container</p>   
                          <hr />   
                      </div>
                    </div>
                    <form className="custom-content-form">
                      <div className="form-row">
                        <div class="form-group col-md-12">
                          <table class="table table-bordered">
                            <thead class="thead-light">
                              <tr>
                                <th scope="col">#</th>
                                <th scope="col">Kg</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <th scope="row">Total weight of water + container (A)</th>
                               
                                <td>
                                  <input
                                    type="text"
                                    className="form-control"
                                    name="s_t1"
                                    value={this.state.s_t1}
                                    onChange={e => this.change(e)}
                                  />
                                </td>
                               
                              </tr>
                              <tr>
                                <th scope="row">Weight of water (B)</th>
                               
                                <td>
                                 <em>calculation [(A-W) Kg]</em>
                                </td>
                              </tr>
                              <tr>
                                <th scope="row">
                                Volume of Density Container (V)
                                </th>
                               
                                <td>
                                <em>calculation [B/1000 cum]</em>
                                </td>
                              </tr>
    
                             
                            </tbody>
                          </table>
                        </div>
                      </div>
                    </form>
                    <hr />
                    <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                          <p>Bulk Density Test</p>   
                          <hr />   
                      </div>
                      <form className="custom-content-form">
                      <div className="form-row">
                        <div class="form-group col-md-12">
                          <table class="table table-bordered">
                            <thead class="thead-light">
                              <tr>
                                <th scope="col">#</th>
                                <th scope="col">DLBD</th>
                                <th scope="col">DLBD</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <th scope="row">Weight of aggregates + container (P)</th>
                               
                                <td>
                                  <input
                                    type="text"
                                    className="form-control"
                                    name="s_t2"
                                    value={this.state.s_t2}
                                    onChange={e => this.change(e)}
                                  />
                                </td>

                                 <td>
                                  <input
                                    type="text"
                                    className="form-control"
                                    name="s_t3"
                                    value={this.state.s_t3}
                                    onChange={e => this.change(e)}
                                  />
                                </td>
                               
                              </tr>
                              <tr>
                                <th scope="row">Weight of aggregates (Q)</th>
                               
                                <td>
                                 <em>calculation [P-W Kg]</em>
                                </td>
                                <td>
                                 <em>calculation [P-W Kg]</em>
                                </td>
                              </tr>
                              <tr>
                                <th scope="row">
                                Density of Aggregate (D)
                                </th>
                               
                                <td>
                                <em>calculation [Q/V Kg/m^3]</em>
                                </td>
                                <td>
                                <em>calculation [Q/V Kg/m^3]</em>
                                </td>
                              </tr>
                              <tr>
                                <th scope="row">
                                % Voids (Specific gravity of sample - D)/(Specific Gravity of sample)
                                </th>
                               
                                <td>
                                <em>calculation [%]</em>
                                </td>
                                <td>
                                <em>calculation [%]</em>
                                </td>
                              </tr>
                              <tr>
                                <th scope="row">
                                Average Bulk Density
                                </th>
                               
                                <td>
                                <em>calculation [%]</em>
                                </td>
                                <td>
                                <em>calculation [%]</em>
                                </td>
                              </tr>
    
                             
                            </tbody>
                          </table>
                        </div>
                      </div>
                    </form>

                  </div>
                  {/*end test detail form */}
                  {/*comment pto */}
                  <div className="below-custom-form">
                    <div className="row">
                      <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <h3>More Details</h3>
                        <hr />
                        <br />
                      </div>
                    </div>
                    <form className="custom-content-form">
                      <div className="form-row">
                        <div class="form-group col-md-12">
                          <div class="form-group row">
                            <label
                              for="inputPassword"
                              class="col-sm-2 col-form-label"
                            >
                              PTO's Comment
                            </label>
                            <div class="col-sm-10">
                              <textarea
                                class="form-control"
                                id="inputPassword"
                                name="pto_comment"
                                value={this.state.pto_comment}
                                onChange={e => this.change(e)}
                              />
                              <span className="text-danger">
                                {this.validator.message(
                                  "PTO's Comment",
                                  this.state.pto_comment,
                                  "required"
                                )}
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>
                      <small>* This Fields are Mandatory . </small>
                      <br />
                      <button
                        class="btn btn-primary"
                        onClick={this.onSubmit.bind(this)}
                      >
                        Submit
                      </button>
                    </form>
                  </div>
                  {/*end comment pto */}
                </div>
              </div>
            </div>
          </div>
        );
      }
    }
export default BulkDensity
