import React, { Component } from "react";
import { Link, Redirect, withRouter, NavLink } from "react-router-dom";
import "./Test.css";
import axios from "axios";
import authService from "../../services/auth-service";
import axiosService from "../../services/axios-service";
import { API_URL } from "../../services/url";
import SimpleReactValidator from "simple-react-validator";
export class Aggregate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      supplier: "",
      source: "",
      sample_location: "",
      weight: "",
      agg_type1: "",
      agg_type2: "",
      agg_type3: "",
      weight_balance: "",
      weight_sample_testing: "",
      weight_sample_sieving: "",
      material_passing: "",
      pto_comment: "",
      total: "",
      amount_status: false,
      plant: "",
      date: ""
    };
    this.onSubmit = this.onSubmit.bind(this);
    this.validator = new SimpleReactValidator();
  }
  componentWillMount = () => {
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth() + 1; //January is 0!
    var yyyy = today.getFullYear();

    if (dd < 10) {
      dd = "0" + dd;
    }

    if (mm < 10) {
      mm = "0" + mm;
    }

    today = mm + "/" + dd + "/" + yyyy;
    console.log("date here", today);
    this.state.date = today;
    console.log(this.state.date);
  };

  change = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  getWebsite = () => {
    fetch("/").then(console.log(this.state));
  };
  async onSubmit(event) {
    event.preventDefault();
    if (this.validator.allValid()) {
      let tokenvalue = localStorage.getItem("token");
      let body = {
        supplier: this.state.supplier,
        source: this.state.source,
        sample_location: this.state.sample_location,
        weight: this.state.weight,
        agg_type1: this.state.agg_type1,
        agg_type2: this.state.agg_type2,
        agg_type3: this.state.agg_type3,
        weight_balance: this.state.weight_balance,
        weight_sample_testing: this.state.weight_sample_testing,
        weight_sample_sieving: this.state.weight_sample_sieving,
        material_passing: this.state.total,
        pto_comment: this.state.pto_comment,
        plant: this.state.plant,
        date: this.state.date,
        form_type: "RF_14"
      };
      console.log(body);
      try {
        const response = await axios.post(
          `${API_URL}rf_form/submit`,
          body,
          (axios.defaults.headers.common["x-access-token"] = tokenvalue)
        );
        console.log(response);
        //   .then(console.log(this.state));

          if (response.status==200) {
            alert(response.data);
            // this.props.history.push("/ViewEmployee");
          } else {
            alert(response.data.msg);
          }
      } catch (error) {
        console.log(error);
      }
    } else {
      this.validator.showMessages();
      // rerender to show messages for the first time
      this.forceUpdate();
    }
  }
  totalshow = e => {
    e.preventDefault();
    let total_sample_sieving = this.state.weight_sample_sieving;
    let total_sample_testing = this.state.weight_sample_testing;
    let total_amount =
      ((total_sample_testing - total_sample_sieving) / total_sample_testing) *
      100;
    this.state.total = total_amount;
    console.log(total_amount);
    this.setState({
      amount_status: true
    });
  };
  render() {
    let display_total_amount;
    if (this.state.amount_status == true) {
      display_total_amount = <div>{this.state.total} %</div>;
    } else {
      display_total_amount = (
        <div>
          <p>Enter The Above Fields</p>
        </div>
      );
    }

    return (
      <div className="skin-blue fixed-layout">
        <div className="page-wrapper">
          <div className="container-fluid">
            <div className="row page-titles">
              <div className="col-md-5 align-self-center">
                {/* <h4 className="text-themecolor">Forms</h4> */}
                <nav aria-label="breadcrumb">
                  <ol class="breadcrumb">
                    <li className="breadcrumb-item">
                      <Link to={process.env.PUBLIC_URL + "/"}>Home</Link>
                    </li>
                    <li className="breadcrumb-item active" aria-current="page">
                      Add Aggregate
                    </li>
                  </ol>
                </nav>
              </div>
            </div>
          </div>

          {/*Form content begin */}

          <div className="product-form-upper">
            <div className="container">
              <div className="below-custom-form">
                <div className="row">
                  <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <div className="custom-btn-lists">
                      <ul>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/ComingSoon"}
                            >
                              Particle Size
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/FLakinessIndex"}
                            >
                              Flakiness Index
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Testtwo"}
                            >
                              Elongation Index
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Testone"}
                            >
                              Slit Content
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Waterabs"}
                            >
                              Gravity and Water abs
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Testthree"}
                            >
                              Surface Moisture
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/Compaction"}
                            >
                              Compaction
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/CrushingValue"}
                            >
                              Crushing
                            </NavLink>
                          </button>
                        </li>
                        <li>
                          <button className="btn btn-info">
                            <NavLink
                              activeClassName="selected"
                              to={process.env.PUBLIC_URL + "/BulkDensity"}
                            >
                              Bulk Density
                            </NavLink>
                          </button>
                        </li>
                      </ul>
                    </div>

                    <hr />
                  </div>

                  <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <p style={{ textAlign: "center" }}>
                      Test Format - RF:14 (Determination of Silt content by wet
                      seiving ){" "}
                    </p>
                    <hr />
                  </div>
                  <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <h3>Add Aggregate</h3>
                    <hr />
                    <br />
                  </div>
                </div>
                <form className="custom-content-form">
                  <div className="form-row">
                    <div class="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-2 col-form-label"
                        >
                          Plant
                        </label>
                        <div class="col-sm-10">
                          <input
                            type="text"
                            class="form-control"
                            id="inputPassword"
                            name="plant"
                            value={this.state.plant}
                            onChange={e => this.change(e)}
                          />
                          <span className="text-danger">
                            {this.validator.message(
                              "plant's name",
                              this.state.plant,
                              "required|alpha_space"
                            )}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-2 col-form-label"
                        >
                          date
                        </label>
                        <div class="col-sm-10">{this.state.date}</div>
                      </div>
                    </div>
                    <div class="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-2 col-form-label"
                        >
                          Supplier
                        </label>
                        <div class="col-sm-10">
                          <input
                            type="text"
                            class="form-control"
                            id="inputPassword"
                            name="supplier"
                            value={this.state.supplier}
                            onChange={e => this.change(e)}
                          />
                          <span className="text-danger">
                            {this.validator.message(
                              "supplier's name",
                              this.state.supplier,
                              "required|alpha_space"
                            )}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-2 col-form-label"
                        >
                          Source
                        </label>
                        <div class="col-sm-10">
                          <input
                            type="text"
                            class="form-control"
                            id="inputPassword"
                            name="source"
                            value={this.state.source}
                            onChange={e => this.change(e)}
                          />
                          <span className="text-danger">
                            {this.validator.message(
                              "source",
                              this.state.source,
                              "required"
                            )}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-2 col-form-label"
                        >
                          Sampling Location
                        </label>
                        <div class="col-sm-10">
                          <input
                            type="text"
                            class="form-control"
                            id="inputPassword"
                            name="sample_location"
                            value={this.state.sample_location}
                            onChange={e => this.change(e)}
                          />
                          <span className="text-danger">
                            {this.validator.message(
                              "Location of Sample",
                              this.state.sample_location,
                              "required|alpha_space"
                            )}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputSubcategory"
                          className="col-sm-2 col-form-label"
                        >
                          Weight of Sample Fot Testing
                        </label>
                        <div class="col-sm-10">
                          <input
                            type="text"
                            class="form-control"
                            id="inputPassword"
                            name="weight"
                            value={this.state.weight}
                            onChange={e => this.change(e)}
                          />
                          <span className="text-danger">
                            {this.validator.message(
                              "Weight Of Sample",
                              this.state.weight,
                              "required|alpha_num_space"
                            )}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-3 col-form-label"
                        >
                          Aggragate Type
                        </label>
                        <div class="col-sm-9">
                          <div class="form-check form-check-inline">
                            <input
                              className="form-check-input"
                              type="checkbox"
                              id="inlineCheckbox1"
                              value={this.state.agg_type1}
                              name="agg_type1"
                              onClick={e => this.change(e)}
                            />
                            <label
                              className="form-check-label"
                              for="inlineCheckbox1"
                            >
                              River Sand
                            </label>
                          </div>
                          <div className="form-check form-check-inline">
                            <input
                              className="form-check-input"
                              type="checkbox"
                              id="inlineCheckbox2"
                              value={this.state.agg_type2}
                              name="agg_type2"
                              onClick={e => this.change(e)}
                            />
                            <label
                              className="form-check-label"
                              for="inlineCheckbox2"
                            >
                              Crushed Sand
                            </label>
                          </div>
                          <div className="form-check form-check-inline">
                            <input
                              className="form-check-input"
                              type="checkbox"
                              id="inlineCheckbox3"
                              value={this.state.agg_type3}
                              name="agg_type3"
                              onClick={e => this.change(e)}
                            />
                            <label
                              className="form-check-label"
                              for="inlineCheckbox3"
                            >
                              Crushed Rock Fines
                            </label>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="form-group col-md-6">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-3 col-form-label"
                        >
                          Weighing Balance Details
                        </label>
                        <div class="col-sm-9">
                          <input
                            type="text"
                            class="form-control"
                            id="inputPassword"
                            name="weight_balance"
                            value={this.state.weight_balance}
                            onChange={e => this.change(e)}
                          />
                          <span className="text-danger">
                            {this.validator.message(
                              "Weight Balance",
                              this.state.weight_balance,
                              "required"
                            )}
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                  {/* <small>* This Fields are Mandatory . </small> */}
                  {/* <br />
                  <button
                    class="btn btn-primary"
                    onClick={this.onSubmit.bind(this)}
                  >
                    Submit
                  </button> */}
                </form>
              </div>
              {/*etst detial form */}
              <div className="below-custom-form">
                <div className="row">
                  <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <h3>Test Details</h3>
                    <hr />
                    <br />
                  </div>
                </div>
                <form className="custom-content-form">
                  <div className="form-row">
                    <div class="form-group col-md-12">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-4 col-form-label"
                        >
                          Weight of Sample Taken for Testing (A)
                        </label>
                        <div class="col-sm-8">
                          <input
                            type="text"
                            class="form-control"
                            id="inputPassword"
                            name="weight_sample_testing"
                            value={this.state.weight_sample_testing}
                            onChange={e => this.change(e)}
                          />
                          <span className="text-danger">
                            {this.validator.message(
                              "weight of sample for testing",
                              this.state.weight_sample_testing,
                              "required"
                            )}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="form-group col-md-12">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-4 col-form-label"
                        >
                          Weight of Sample Retained After Sieving (B)
                        </label>
                        <div class="col-sm-8">
                          <input
                            type="text"
                            class="form-control"
                            id="inputPassword"
                            name="weight_sample_sieving"
                            value={this.state.weight_sample_sieving}
                            onChange={e => this.change(e)}
                          />
                          <span className="text-danger">
                            {this.validator.message(
                              "weight of sample for sieving",
                              this.state.weight_sample_sieving,
                              "required"
                            )}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div class="form-group col-md-12">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-4 col-form-label"
                        >
                          Material Passing
                          <br />
                          (A-B)/A*100
                        </label>
                        <div class="col-sm-8">
                          {/* <p>para here </p> */}
                          {display_total_amount}
                          {/* {this.state.total || 'NO DATA'} */}
                        </div>
                      </div>
                      <button
                        className="btn btn-danger"
                        onClick={e => this.totalshow(e)}
                      >
                        Total
                      </button>
                    </div>
                  </div>
                  {/* <small>* This Fields are Mandatory . </small> */}
                  {/* <br />
                  <button
                    class="btn btn-primary"
                    onClick={this.onSubmit.bind(this)}
                  >
                    Submit
                  </button> */}
                </form>
              </div>
              {/*end test detail form */}
              {/*comment pto */}
              <div className="below-custom-form">
                <div className="row">
                  <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                    <h3>More Details</h3>
                    <hr />
                    <br />
                  </div>
                </div>
                <form className="custom-content-form">
                  <div className="form-row">
                    <div class="form-group col-md-12">
                      <div class="form-group row">
                        <label
                          for="inputPassword"
                          class="col-sm-2 col-form-label"
                        >
                          PTO's Comment
                        </label>
                        <div class="col-sm-10">
                          <textarea
                            class="form-control"
                            id="inputPassword"
                            name="pto_comment"
                            value={this.state.pto_comment}
                            onChange={e => this.change(e)}
                          />
                          <span className="text-danger">
                            {this.validator.message(
                              "PTO's Comment",
                              this.state.pto_comment,
                              "required"
                            )}
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                  <small>* This Fields are Mandatory . </small>
                  <br />
                  <button
                    class="btn btn-primary"
                    onClick={this.onSubmit.bind(this)}
                  >
                    Submit
                  </button>
                </form>
              </div>
              {/*end comment pto */}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Aggregate;
